.rmc_carousel {
  position: relative;
  overflow: hidden;
}
.rmc_carousel_list {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  margin: 0px;
  padding: 0px;
  cursor: inherit;
  box-sizing: border-box;
}
.rmc_carousel_item {
  -ms-touch-action: none;
      touch-action: none;
  vertical-align: top;
  text-align: center;
  height: auto;
  box-sizing: border-box;
}
.rmc_carousel_item_inner {
  display: inline-block;
}
.rmc_carousel_item img {
  width: 100%;
  display: block;
}
.rmc_carousel_single_item {
  width: 100%;
}
.rmc_carousel_dot {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
}
.rmc_carousel_dot_point {
  display: inline-block;
  zoom: 1;
}
.rmc_carousel_dot_point span {
  display: block;
  width: .5rem;
  height: .5rem;
  margin: 0 .3rem;
  border-radius: 50%;
  background: #ccc;
}
.rmc_carousel_dot_point.rmc_dot_point_active span {
  background: #f15440;
}
